import React, { Component } from 'react';
import Aux from '../../../hoc/Aux/Aux';
import classes from './SideDrawer.css';
import SideItem from './SideItem/SideItem';
import { connect } from 'react-redux';
import * as actions from '../../../store/actions/index';
import dummyData from '../../../store/dummy/dummyData';


let itemsArray = dummyData;
class SideDrawer extends Component {

    clickHandler = (id, active) => {
        this.props.onTest(id, active);

    };

    onSelectAll = () => {
        this.props.onSelectAllItems(dummyData);
    };

    onSelectNone = () => {
        this.props.onSelectNoneItems(dummyData);
    };


    render() {
        this.props.onTest(itemsArray[0].id, itemsArray[0]);


        let listItems = itemsArray.map(itemElement => (
            <SideItem
                key={itemElement.id}
                itemId={itemElement.id}
                name={itemElement.label}
                timeRange={itemElement.timeRange}
                active={itemElement.active}
                color={itemElement.borderColor}
                clicked={() => this.clickHandler(itemElement.id, itemElement)}
            />
        ));
        console.clear();

        const pronampeList = listItems.filter((data) => {
            return (data.props.name.toLowerCase().includes("pronampe"));
        })

        const facebookPayList = listItems.filter((data) => {
            return (data.props.name.toLowerCase().includes("facebook"));
        })

        const botNegociadorList = listItems.filter((data) => {
            return (data.props.name.toLowerCase().includes("bot negociador"));
        });

        const onSetSubject = (id) => {
            let items, i, items2, h;
            items = document.querySelectorAll(".SideDrawer__SideDrawerSubjectList__NFhgx");
            for (i = 0; i < items.length; i++) {
                items[i].style.display = "none"
            }
            items2 = document.querySelectorAll(".SideDrawer__SideDrawerSubject__3V-za");
            for (h = 0; h < items2.length; h++) {
                items2[h].style.backgroundColor = "#b0d3ec";
            }
            document.querySelector(id).style.display = "inherit"
            document.querySelector(id + "Title").style.backgroundColor = "#efe31a";
        }



        return (
            <Aux>
                <div className={classes.SideDrawer}>
                    <section className={classes.SideDrawerTop}>
                        <h1 className={classes.SideDrawerTitle}>Selecione um assunto abaixo:</h1>
                    </section>

                    <ul className={classes.SideDrawerList}>
                        <li id="pronampeListTitle" className={classes.SideDrawerSubject} onClick={() => onSetSubject(`#pronampeList`)}>Pronampe</li>
                        <span id="pronampeList" className={classes.SideDrawerSubjectList}>
                            {pronampeList}
                        </span>

                        <li id="facebookPayListTitle" className={classes.SideDrawerSubject} onClick={() => onSetSubject(`#facebookPayList`)}>Facebook Pay</li>
                        <span id="facebookPayList" className={classes.SideDrawerSubjectList}>
                            {facebookPayList}
                        </span>

                        <li id="botNegociadorListTitle" className={classes.SideDrawerSubject} onClick={() => onSetSubject(`#botNegociadorList`)}>Bot Negociador</li>
                        <span id="botNegociadorList" className={classes.SideDrawerSubjectList}>
                            {botNegociadorList}
                        </span>

                    </ul>
                </div>
            </Aux>
        );

    }
}

const mapDispatchToProps = dispatch => {
    return {
        onTest: (id, active) => dispatch(actions.fetchIngredientsFailed(id, active)),
        onSelectAllItems: (items) => dispatch(actions.selectAll(items)),
        onSelectNoneItems: (items) => dispatch(actions.selectNone(items)),
    };
};


export default connect(null, mapDispatchToProps)(SideDrawer);




