import React, { Component } from 'react';
import { connect } from 'react-redux';
import Aux from '../../hoc/Aux/Aux';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import axios from '../../axios-orders';
import classes from './ChartLine.css'
import Chart from 'chart.js'
// 
import 'chartjs-plugin-funnel';



let myChart;
export class chartLine extends Component {

    renderChart = () => {
        let ctx = document.getElementById('myChart').getContext('2d');
        myChart = new Chart(ctx, {
            type: 'funnel',
            data: this.props.chartData[0],
            options: {
                sort: 'desc',
                bezierCurve: true,
                responsive: true,
                hoverMode: 'index',
                stacked: false,
                hover: {
                    animationDuration: 1
                },
                animation: {
                    duration: 1
                },
                legend: {
                    display: false,
                },
                scales: {
                    yAxes: [{
                        display: true,
                        id: 'y-axis-1',
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                        ticks: {
                            beginAtZero: true,
                            fontColor: "black",
                            fontSize: 14
                        },
                    }],
                    xAxes: [{
                        ticks: {
                            beginAtZero: true,
                            fontColor: "black",
                            padding: 16
                        },
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                    }],
                },
                tooltips: {
                    // yAlign: 'bottom',
                    mode: 'single',
                    bodySpacing: 6,
                    titleSpacing: 6,
                    footerSpacing: 6,
                    xPadding: 12,
                    yPadding: 12,
                    titleFontSize: 14,
                    bodyFontSize: 14,
                    backgroundColor: "black",
                    footerFontStyle: 'normal',
                    callbacks: {
                        label: function (tooltipItems, data) {
                            return '   ' + tooltipItems.label + ': ' + tooltipItems.value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
                        }
                    }
                },

            }
        });
        //console.clear();
        this.mountLabelsInnerChart();

    }

    componentDidMount() {
        this.renderChart();
    }

    componentWillReceiveProps() {
        setTimeout(() => {
            myChart.data = this.props.chartData[0];
            myChart.update();
            this.mountLabelsInnerChart();
            //console.clear();
        }, 100);

    }

    mountLabelsInnerChart = () => {
        const innerLabelsContainer = document.querySelector('#myChartInsideLabels');
        //console.log(innerLabelsContainer);
        innerLabelsContainer.style.left = (Math.round(myChart.chartArea.left) + ((Math.round(myChart.width - myChart.chartArea.left)) / 2)) + 'px';
        innerLabelsContainer.style.height = Math.round(myChart.height - (myChart.chartArea.top * 2)) + 'px';
        // innerLabelsContainer.style.width = Math.round(myChart.width - myChart.chartArea.left) + 'px';
        innerLabelsContainer.style.width = '1px';
        innerLabelsContainer.style.top = Math.round(myChart.chartArea.top) + 'px';
        //
        innerLabelsContainer.innerHTML = '';
        let spliceHeight;
        //
        let sumAllValues = 0;
        // myChart.config.data.datasets[0].data.forEach((element, index) => {
        //     sumAllValues += Math.round(element);
        // });
        sumAllValues = myChart.config.data.datasets[0].data[0];

        spliceHeight = Math.round(100 / myChart.config.data.datasets[0].data.length);
        myChart.config.data.datasets[0].data.forEach((element, index) => {
            innerLabelsContainer.innerHTML += (
                `<div style="height:${spliceHeight}%;">
                    <p style="width: 128px;position: relative;float: left;left: 50%;margin-left: -64px; text-align: center; top: 35%; text-shadow: 0px 1px 1px rgba(0,0,0,0.8); color:#f9ea0f; font-size:18px; font-weight: 700;">${element.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}</p>

                    <p style="width: 64px; position: relative; float: left; left: 50%; margin-left: -32px; text-align: center; top: 64%; color: #fff; font-size: 12px; border-radius: 32px; background: rgb(0 0 0 / 0.7); padding: 4px 0px;">${((element * 100) / sumAllValues).toFixed(2)}%</p>

                </div>`)

        });
    }




    render() {
        const self = this;
        document.getElementsByTagName("BODY")[0].onresize = function () {
            setTimeout(() => {
                self.mountLabelsInnerChart();
            }, 100);
        };

        return (
            <Aux>
                <div className={classes.ChartLine}>
                    <div id="myChartInsideLabels" className={classes.ChartLineInsideLabels}></div>
                    <canvas id="myChart"></canvas>
                </div>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        chartData: state.chartLine.chartData,
        up: state.chartLine.up,
    };
};

const mapDispatchToProps = dispatch => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(chartLine, axios));


